const install = (Vue, vm) => {
	
	/**
	 * 
	 */
	const isLogin = () => {
		// token, 
		const token = vm.vuex_token
		if (!token) {
			// 
			const currentPage = getCurrentPages().pop()
			// , 
			const {options, route} = currentPage
			// key
			const optionsKeys = Object.keys(options)
			let params = ''
			if (optionsKeys.length !== 0) {
				params = optionsKeys.reduce((pre, current) => {
					return `${pre}${current}=${options[current]}&`
				}, '?').slice(0, -1)
			}
			// , 
			uni.setStorageSync('back_url', route + params)
			
			vm.$u.toast('')
			setTimeout(() => {
				vm.$u.route({
					type: 'redirect',
					url: 'pages/auth/login',
				})
			}, 1500)
			return false
		}
		
		return true
	}
	
	/**
	 * , vuex_user
	 */
	const updateUser = async () => {
		// 
		const userInfo = await vm.$u.api.userInfo()
		// 
		vm.$u.vuex('vuex_user', userInfo)
	}
	
	/**
	 * 
	 */
	const isAndroid = () => {
		try {
		    const res = uni.getSystemInfoSync();
		    if(res.platform === 'android') return true
				return false
		} catch (e) {
		    // error
		}
	}
	
	/**
	 * 
	 */
  const networkType = () => {
      try {
        let results = true
        uni.getNetworkType({
          success: function (res) {
            if(res.networkType=='2g' || res.networkType=='3g' || res.networkType=='none'){
                console.log(":",res.networkType);
                vm.$u.toast('~')
                results = false
            }else{
              results = true
            }
          },
        });
        return results
      } catch (e) {
          // error
      }
  }
  
	vm.$u.utils = {
		isLogin,
		updateUser,
		isAndroid,
    networkType
	}
}

export default {
	install
}